*, :before, :after {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  font-size: 100%;
}

.info-section {
  min-height: 40vh;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  margin: 2em 2em 5em;
  padding: 2em;
  display: flex;
}

.info-section .w3c-svg {
  width: 9em;
  height: 5em;
}

.info-section .w3c-svg svg {
  width: 100%;
  height: auto;
}

.info-section > * {
  margin: 2em;
}

.technology-section {
  margin-top: 8em;
}

.technology-section h2 {
  text-align: center;
  margin: 0;
}

.technology-section .img-list {
  min-height: 20vh;
  flex-flow: wrap;
  flex: auto;
  justify-content: center;
  align-items: center;
  padding: 2em;
  display: flex;
}

.technology-section .img-list .bootstrap-svg {
  filter: grayscale();
  width: 5.625em;
  height: 5.625em;
  align-self: center;
}

.technology-section .img-list .bootstrap-svg svg {
  fill: #712cf9;
}

.technology-section .img-list .bootstrap-svg:hover {
  filter: grayscale(0%);
  transition: all .2s linear;
}

.technology-section .img-list .bootstrap-svg:hover .icon-label {
  display: block;
}

.technology-section .img-list .sass-svg {
  filter: grayscale();
  width: 5.625em;
  height: 5.625em;
}

.technology-section .img-list .sass-svg svg {
  max-width: 100%;
  height: auto;
}

.technology-section .img-list .sass-svg:hover {
  filter: grayscale(0%);
  transition: all .2s linear;
}

.technology-section .img-list .sass-svg:hover .icon-label {
  display: block;
}

.technology-section .img-list .responsive-svg {
  filter: grayscale();
  width: 5.625em;
  height: 5.625em;
  align-self: center;
}

.technology-section .img-list .responsive-svg svg {
  width: 100%;
  height: auto;
}

.technology-section .img-list .responsive-svg:hover {
  filter: grayscale(0%);
  transition: all .2s linear;
}

.technology-section .img-list .responsive-svg:hover .icon-label {
  display: block;
}

.icon-label {
  text-align: center;
  margin-top: .5em;
  padding: 0;
  display: none;
}

.img-list > * {
  margin: 0 2em;
}

@media screen and (max-width: 300px) {
  .info-section {
    margin: 0;
  }
}

@media screen and (max-width: 600px) {
  .img-list > * {
    margin-bottom: 1.5em;
  }

  .icon-label {
    display: none !important;
  }
}

